<template>

  <div class="dashboard">

    <v-snackbar transition="scale-transition" v-model="snackbarElimina" :timeout="4000" top color="error">
      <span class="white--text">
        <v-icon class="ma-2" right medium> mdi-minus-circle </v-icon>
        Rimosso con successo!
        </span>
      <template v-slot:action="{ attrs }">
      <v-btn color="white" v-bind="attrs" text @click="snackbarElimina = false">Chiudi</v-btn>
      </template>
    </v-snackbar>

    <v-snackbar transition="scale-transition" v-model="snackbar" :timeout="4000" top color="success">
      <span class="white--text">
        <v-icon class="ma-2" right medium> mdi-checkbox-marked-circle </v-icon>
        Aggiunto con successo!</span>
        <template v-slot:action="{ attrs }">
         <v-btn color="white" v-bind="attrs" text @click="snackbar = false">Chiudi</v-btn>
        </template>
    </v-snackbar>

    <h1 class="subtitle-1 grey--text">Dashboard</h1>

    <v-container class="my-5">

      <v-row class="mb-3">
        <v-tooltip top>
          <template v-slot:activator="{on}">
        <v-btn small depressed class="grey--text" @click="sortBycompletate('stato')" v-on="on">
          <v-icon left small>mdi-swap-vertical</v-icon>
          <span class="caption text-lowercase">Completate</span>
        </v-btn>
          </template>
          <span> Ordina le schede completate</span>
        </v-tooltip>
         <v-tooltip top>
          <template v-slot:activator="{on}">
        <v-btn small depressed class="grey--text" @click="sortByinCorso('stato')" v-on="on">
          <v-icon left small>mdi-swap-vertical</v-icon>
          <span class="caption text-lowercase">In corso</span>
        </v-btn>
          </template>
          <span> Ordina le schede in corso</span>
        </v-tooltip>
        <v-col>
        <div align="right">
        <Popup @projectAdded="snackbar = true"/>
        </div>
        </v-col>
      </v-row>
      

       <v-card flat tile class="pa-3" v-for="project in projects" :key="project.titolo">
         <v-row row wrap :class="`pa-3 project ${project.stato}`">
           <v-col cols="12" md="7">
             <div class="caption grey--text">Ricordati di:</div>
             <div>{{ project.titolo }}</div>
           </v-col>
           <v-col xs="2">
             <div class="caption grey--text">Entro il</div>
             <div>{{ project.entro }}</div>
           </v-col>
           
           <v-col xs="2">
             <div align="right">
               <v-menu offset-y>
                 <template v-slot:activator="{ on, attrs }">
                  <v-chip v-bind="attrs" v-on="on" small :color="`${project.stato}`" :class="`v-chip--active white--text caption my-2`">{{ project.stato }}</v-chip>
                 </template>
                 <v-list>
					<v-list-item @click="cambiaStato(project.titolo)">
						<v-list-item-title>completato</v-list-item-title>
					</v-list-item>
				</v-list>
                </v-menu>
             </div>
           </v-col>
           <v-col xs="2">
             <div align="right">
             <v-btn small icon @click="cancella(project.id), popupSnackbarElimina()">
               <v-icon color="red darken-1">mdi-delete</v-icon>
             </v-btn>
             </div>
           </v-col>
         </v-row>
         <v-row cols="12">
          <v-divider></v-divider>
         </v-row>
       </v-card>

   </v-container>

  </div>
</template>

<script>
import Popup from '../components/Popup.vue'
import db from '@/fb'
// @ is an alias to /src

export default {
  components:{ Popup },
  data(){
    return{
      projects: [],
      snackbar: false,
      snackbarElimina: false
    }
  },
  methods: {

    cambiaStato(projectTitolo){
      db.collection("projects").where("titolo", "==", projectTitolo)
    .get()
      .then(function(querySnapshot){
        querySnapshot.forEach(function(doc){
          doc.ref.update({stato: 'completato'});
        });
      }) 
    },

    popupSnackbarElimina() {
      this.snackbarElimina = true;
    },

    sortBycompletate(prop){
      this.projects.sort((c,i) => i[prop] > c[prop] ? -1 : 1)
    },
    sortByinCorso(prop){
      this.projects.sort((c,i) => i[prop] < c[prop] ? -1 : 1)
    },

    cancella(id){
      console.log('deleted');
      db.collection('projects').doc(id).delete()
    }
  },
  created(){

    var t = this

	db.collection('projects').onSnapshot(res => {
		const changes = res.docChanges()
		changes.forEach(change => {
			if (change.type === 'added') {
				this.projects.push({
					...change.doc.data(),
					id: change.doc.id
				})
			}
			if (change.type === 'modified') {
				t.projects.forEach(function(item, index, arr) {
					if (item.id === change.doc.id) {
						arr[index] = {
							...change.doc.data(),
							id: change.doc.id
						}
						console.log('changed: ', arr[index])

						t.projects = [...t.projects]

						return true
					}
				})
			}
			if (change.type === 'removed') {
				t.projects = t.projects.filter(item => item.id !== change.doc.id)
			}
		})
	})
}
}
</script>

<style>

.project.completato{
  border-left: 4px solid #3cd1c2;
}

.project.incorso{
  border-left: 4px solid orange;
}

.project.inritardo{
  border-left: 4px solid tomato;
}

.v-chip.completato{
  background: #3cd1c2;
}

.v-chip.incorso{
  background: #ffaa2c;
}

.v-chip.inritardo{
  background: #f83e70;
}
</style>