<template>
    <v-dialog max-width="600px" v-model="dialog">
        <template v-slot:activator="{on}">
            <v-btn text class="success" v-on="on">Aggiungi</v-btn>
        </template>
        <v-card>
            <v-card-title>
                Aggiungi Nota
            </v-card-title>
            <v-card-text>
                <v-form class="px-3" ref="form">
                    <v-textarea label="Cosa vuoi annotare?" v-model="titolo" prepend-icon="mdi-pencil" :rules="inputRegole"></v-textarea>
  
                    <v-menu max-width="290">
                        <template v-slot:activator="{ on }">
                            <v-text-field :rules="inputRegole" :value="formattedDate" label="Entro il" prepend-icon="mdi-calendar-range" v-on="on"></v-text-field>
                        </template>
                            <v-date-picker v-model="entro"></v-date-picker>
                    </v-menu>

                    <v-btn text class="success mx-0 mt-3" @click="submit" :loading="loading">Aggiungi nota</v-btn>
                </v-form>
            </v-card-text>
        </v-card>
    </v-dialog>
</template>

<script>
import db from '@/fb.js'
export default {
    data(){
        return {
            titolo: '',
            entro: null,
            stato: 'incorso',
            inputRegole: [
                v =>  v && v.length >= 3 || 'Inserire almeno 3 caratteri'
            ],
            loading: false,
            dialog: false
        }
    },
    methods: {
        submit(){
            if(this.$refs.form.validate()){
                this.loading  = true;

                const project = {
                    titolo: this.titolo,
                    entro: this.entro,
                    stato: this.stato
                }

                db.collection('projects').add(project).then(() => {
                    this.loading = false;
                    this.dialog = false;
                    this.$emit('projectAdded')
                })

                this.$refs.form.reset()
            }
        }
    },
    computed: {
        formattedDate(){
            return this.entro ? new Date(this.entro).toUTCString().substring(0,16) : ''
        }
    }
}
</script>